
.pages {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 40px;
    grid-row-gap: 40px;
    max-width: 1180px;
}
.pages li {
}
.pages a {
    color: #bba9c7;
}
.pages li div {
    background-color: #f8f5fa;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    height: 150px;
}
.pages li p {
    margin: 10px 0 5px;
    font: bold 18px 'Open Sans';
    color: #411e56;
}
.pages li caption {
    display: block;
    font: normal 14px 'Open Sans';
    color: #566266;
    text-align: left;
}



